const $ = window.jQuery;

module.exports = {
    ready: function () {
        this.sidebar();
        this.optionForm();
        this.editorButton();
    },
    sidebar: function () {
        $('.moco-nav a').on('click', function (e) {
            let   t       = $(this),
                    p       = t.parent('li'),
                    parent       = p.parent().parent('li.is-sub-menu'),
                    target  = $('#moco-content-' + p.data('target')),
                    content  = $('.moco-content .moco-tab-content.show'),
                    href    = t.attr('href');



            if (href !== undefined) return;

            if (parent.length){

                content.removeClass('show');
                $('li.active', parent).removeClass('active');
                if (target.length){
                    target.addClass('show')
                }
                p.addClass('active');
                return;
            }

            if (!p.hasClass('active')){

                if (!p.hasClass('is-sub-menu')){
                    content.removeClass('show');
                    if (target.length){
                        target.addClass('show')
                    }
                }

                $('.moco-nav li.active').removeClass('active');
                p.addClass('active')
            } else if (p.hasClass('is-sub-menu')){
                p.removeClass('active')
            }
        })
    },
    optionForm: function () {
        $('#moco-framework form').submit(function (e) {
            let   t = $(this),
                    btn = $('.btn-submit', t),
                    data = t.serialize();

            e.preventDefault();
            $.ajax({
                type : "post",
                dataType : "json",
                url : moco.ajax_url,
                data : data,
                beforeSend: function() {
                    btn.addClass('loading');
                    btn.attr('disabled', 'disabled');
                },
                complete: function() {
                    btn.removeClass('loading');
                    btn.removeAttr('disabled');
                },
                success: function(response) {

                    if (response.success){
                        $.niceToast.success(response.message)
                    }else{
                        $.niceToast.error(response.message)
                    }
                }
            })
        })
    },
    editorButton: function (text, id) {

    }

};
